import { List, InputItem} from "jw-components-mobile";
import React from 'react';
import ReactDOM from 'react-dom';

import { createForm } from 'rc-form';

const InputExample = React.createClass({
  
  render() {
    const { getFieldProps } = this.props.form;
    return (
      <div>
        <List className="jw-list">
          <InputItem
            className="jw-inline"
            clear
            placeholder=""            
            autoFocus>
            
          </InputItem>
          <InputItem
            prefixListCls="jw"
            placeholder=""
          ></InputItem>
        </List>
        <List className="input-container jw-list">
          <InputItem prefixListCls="jw-statcked" placeholder="input">
           <i className="fa fa-search"></i>
          </InputItem>
        </List>
        <List className="jw-list">
          <InputItem
           placeholder=""
           data-seed="logId"
          ></InputItem>
          <InputItem
           placeholder=""
           data-seed="logId"
          ></InputItem>
        </List>
      </div>
    );
  },
});

const BasicInputExample = createForm()(InputExample);

ReactDOM.render(<BasicInputExample />, document.getElementById('root'));



